<template>
  <div>
    <div class="c-chart-container col-lg-12">
      <canvas class="my-4 w-100 c-line-chart" id="line-chart"></canvas>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import Chart from 'chart.js';

export default {
  methods: {
    setChart() {
      const ctx = document.getElementById('line-chart');
      const xAxis = [];
      const datas = [];
      const chart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: xAxis,
          datasets: [
            {
              label: '指數總和',
              data: datas,
              backgroundColor: 'rgba(0, 123, 255, 0.5)',
            },
          ],
        },
        responsive: true,
        options: {
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: false,
                },
                scaleLabel: {
                  display: true,
                  labelString: '監測指數',
                },
              },
            ],
          },
        },
      });
      //
      const appendXAxis = () => { // 移除前面的資料表現
        const date = new Date();
        if (xAxis.length > 10) {
          xAxis.shift();
          datas.shift();
        }
        xAxis.push( // 取得現在時間
          `${date.getHours() < 10 ? '0' + date.getHours() : date.getHours()}:${
            date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
          }:${date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()}`,
        );
      };
      const appendDatas = () => { // 假資料
        datas.push(Math.floor(Math.random() * 100));
      };
      const updateChart = () => { // 持續更新資料
        appendXAxis();
        appendDatas();
        chart.update();
      };
      setInterval(() => updateChart(), 1000);
    },
  },
  mounted() {
    this.setChart();
  },
};
</script>
